.b-schedule-context-canvas {

    .b-allocation-cell-editor {
        z-index                                  : 10;
        box-shadow                               : none;
        text-align                               : center;

        /* Materials outlined fields have transparent background, we do not want the SVG content to show through */
        --b-text-field-outlined-background       : var(--b-neutral-100);
        --b-text-field-outlined-focus-background : var(--b-neutral-100);

        .b-number-field {
            .b-spin-trigger {
                display : none;
            }
        }
    }

}

/* Hide context outline during edit */
.b-resource-utilization.b-editing-allocation-cell .b-active-tick {
    display : none;
}
