:root, :host {
    /**
     * Timeline widget label font size
     */
    --b-timeline-label-font-weight : 500;

    /**
     * Timeline widget background color
     */
    --b-timeline-background        : transparent;

    /**
     * Timeline widget border color
     */
    --b-timeline-border-color      : var(--b-grid-cell-border-color);

    /**
     * Timeline widget label color
     */
    --b-timeline-label-color       : var(--b-text-3);
}

.b-timeline {
    overflow                   : visible;

    background                 : var(--b-timeline-background);

    --b-grid-header-background : transparent;

    .b-grid-panel-body {
        padding : 2em 10em 4em 10em;
    }

    .b-virtual-scrollers, .b-yscroll-pad {
        display : none !important;
    }

    .b-sch-header-row:not(:first-child) {
        display : none;
    }

    .b-grid-row {
        background    : transparent;
        border-bottom : 1px solid transparent;
    }

    .b-grid-cell {
        border-block : 1px solid var(--b-timeline-border-color);
    }

    /*  Special milestone styling  */
    /* .b-milestone-wrap .b-sch-event-wrap { */
    /*     z-index : $ event-zindex+5; */

    /*     .b-sch-event:not(.b-sch-event-with-icon) .b-sch-event-content { */
    /*         background-color : $ schedulerpro-milestone-background-color; */
    /*     } */

    /*     label { */
    /*         font-weight : 400; */
    /*         font-size   : 12px; */
    /*     } */
    /* } */

    /* EOF Special milestone styling  */

    .b-sch-header-time-axis-cell {
        justify-content : flex-start;
        padding         : 0 0 0.5em 0.5em;
        border          : none;
        background      : transparent;

        font-weight     : var(--b-timeline-label-font-weight);
        color           : var(--b-timeline-label-color);

        &:before {
            content             : ' ';
            position            : absolute;
            inset-inline-start  : 0;
            bottom              : 0;
            height              : 0.7em;

            border-inline-start : 1px solid var(--b-timeline-border-color);
        }
    }

    /*  Make sure milestones + their label are visible  */
    .b-scheduler-base,
    .b-grid-body-container,
    .b-grid-vertical-scroller,
    .b-grid-sub-grid,
    .b-sch-foreground-canvas {
        overflow : visible !important;
        contain  : none !important;
    }

    /* Override Firefox exception in Grid /resources/ sass /grid/ view / subgrid . scss  */
    /* .b-gridbase .b-firefox:not(.b-overlay-scrollbar) .b-grid-sub-grid .b-grid-horizontal-scroller & .b-gridbase .b-firefox { */
    /*     .b-grid-sub-grid { */
    /*         &.b-grid-horizontal-scroller { */
    /*             overflow : visible !important; */
    /*         } */
    /*     } */
    /* } */

    /* .b-grid-header-container, */
    /* .b-grid-header-container.b-grid-vertical-overflow, */
    /* .b-sch-header-time-axis-cell, */
    /* .b-grid-header-container .b-sch-time-axis-column, */
    /* .b-grid-header-container .b-sch-time-axis-column:hover { */
    /*     background : transparent !important; */
    /*     box-shadow : none; */
    /*     border     : none; */
    /* } */

    .b-grid-header {
        min-height : 1.5em;
    }

    .b-grid-vertical-overflow .b-grid-header-scroller:last-child {
        border-right : none;
    }

    /* Labels  */
    .b-timeline-start-date {
        inset-inline-start : -1em;
        translate          : -100%;
    }

    .b-timeline-end-date {
        inset-inline-end : -1em;
        translate        : 100%;
    }

    .b-timeline-start-date,
    .b-timeline-end-date {
        position        : absolute;
        top             : 0;
        display         : flex;
        align-items     : center;
        justify-content : center;
        min-width       : 6em;

        color           : var(--b-timeline-label-color);
        font-size       : var(--b-grid-header-font-size);
        font-weight     : var(--b-timeline-label-font-weight);
    }

}
