.b-calendar-editor-content {
    .b-calendar-editor-date-info {
        /* https://github.com/bryntum/support/issues/9475 */
        /* conflicts with `.b-widget { display : inline-flex }` in docs, need to prioritize */
        /* avoiding `!important` by adding specificity (the wrapping `.b-calendar-editor-content` class) */
        display : block;
    }
}

.b-calendar-editor-date-info {
    padding : 0.4em;

    .b-time-ranges {
        padding-inline-start : 2em;
    }

    .b-intervals {
        padding-inline-start : 0.8em;

        li {
            display     : flex;
            align-items : center;
            gap         : 1em;

            &.b-interval:hover {
                cursor          : pointer;
                text-decoration : underline;
            }

            &:before {
                content       : '';
                width         : 1em;
                height        : 1em;
                border-radius : var(--b-widget-border-radius);
            }

            &.b-interval:before {
                background-color : var(--b-primary);
                display          : inline-block;
            }

            &.b-interval:hover:before {
                text-decoration : none;
            }
        }
    }

    .b-date-text {
        font-weight : bold;
    }
}
