/* @after SchedulerPro/view/ResourceHistogram.css */

:root, :host {
    /**
     * Bar legend font size
     */
    --b-resource-utilization-bar-legend-font-size                      : .9em;

    /**
     * Resource bar legend font size
     */
    --b-resource-utilization-resource-bar-legend-font-size             : .9em;

    /**
     * Assignment bar legend font size
     */
    --b-resource-utilization-assignment-bar-legend-font-size           : .9em;

    /**
     * Bar legend text fill (SVG text)
     */
    --b-resource-utilization-bar-legend-text-color                     : var(--b-grid-cell-color);

    /**
     * Default bar color
     */
    --b-resource-utilization-bar-color                                 : color-mix(in srgb, var(--b-color-green), var(--b-mix) 95%);

    /**
     * Default bar hover color
     * @state Hovered
     */
    --b-resource-utilization-bar-hover-color                           : color-mix(in srgb, var(--b-color-green), var(--b-mix) 90%);

    /**
     * Under-allocated bar color
     */
    --b-resource-utilization-underallocated-bar-color                  : color-mix(in srgb, var(--b-color-green), var(--b-mix) 90%);

    /**
     * Under-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-underallocated-bar-hover-color            : color-mix(in srgb, var(--b-color-green), var(--b-mix) 85%);

    /**
    * Under-allocated text color
    */
    --b-resource-utilization-underallocated-text-color                 : color-mix(in srgb, var(--b-color-green), var(--b-opposite) 50%);

    /**
     * Over-allocated bar color
     */
    --b-resource-utilization-overallocated-bar-color                   : color-mix(in srgb, var(--b-color-red), var(--b-mix) 90%);

    /**
     * Over-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-overallocated-bar-hover-color             : color-mix(in srgb, var(--b-color-red), var(--b-mix) 85%);

    /**
     * Over-allocated text color
     */
    --b-resource-utilization-overallocated-text-color                  : color-mix(in srgb, var(--b-color-red), var(--b-opposite) 50%);

    /**
     * Resource bar legend text fill (SVG text)
     */
    --b-resource-utilization-resource-bar-legend-text-color            : var(--b-resource-utilization-bar-legend-text-color);

    /**
     * Default resource bar color
     */
    --b-resource-utilization-resource-bar-color                        : var(--b-resource-utilization-bar-color);

    /**
     * Default resource bar hover color
     * @state Hovered
     */
    --b-resource-utilization-resource-bar-hover-color                  : var(--b-resource-utilization-bar-hover-color);

    /**
     * Resource over-allocated bar color
     */
    --b-resource-utilization-resource-overallocated-bar-color          : var(--b-resource-utilization-overallocated-bar-color);

    /**
     * Resource over-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-resource-overallocated-bar-hover-color    : var(--b-resource-utilization-overallocated-bar-hover-color);

    /**
     * Resource over-allocated text color
     */
    --b-resource-utilization-resource-overallocated-text-color         : var(--b-resource-utilization-overallocated-text-color);

    /**
     * Resource under-allocated bar color
     */
    --b-resource-utilization-resource-underallocated-bar-color         : var(--b-resource-utilization-underallocated-bar-color);

    /**
     * Resource under-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-resource-underallocated-bar-hover-color   : var(--b-resource-utilization-underallocated-bar-hover-color);

    /**
     * Resource under-allocated text color
     */
    --b-resource-utilization-resource-underallocated-text-color        : var(--b-resource-utilization-underallocated-text-color);

    /**
     * Assignment bar legend text fill (SVG text)
     */
    --b-resource-utilization-assignment-bar-legend-text-color          : var(--b-resource-utilization-bar-legend-text-color);

    /**
     * Default assignment bar color
     */
    --b-resource-utilization-assignment-bar-color                      : transparent;

    /**
     * Default assignment bar hover color
     * @state Hovered
     */
    --b-resource-utilization-assignment-bar-hover-color                : transparent;

    /**
     * Assignment over-allocated bar color
     */
    --b-resource-utilization-assignment-overallocated-bar-color        : transparent;

    /**
     * Assignment over-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-assignment-overallocated-bar-hover-color  : transparent;

    /**
     * Assignment under-allocated bar color
     */
    --b-resource-utilization-assignment-underallocated-bar-color       : transparent;

    /**
     * Assignment under-allocated bar hover color
     * @state Hovered
     */
    --b-resource-utilization-assignment-underallocated-bar-hover-color : transparent;
}

.b-resource-utilization {
    .b-resource-utilization-cell {
        padding                 : 0;
        border-inline-end-width : 0;
        background              : transparent;
    }
}

.b-resource-utilization-histogram {

    padding          : 0;
    background-color : transparent;

    svg g text.b-bar-legend {
        writing-mode       : lr;
        alignment-baseline : middle;
        text-anchor        : middle;
        /* Prevent tooltip from reacting to moving over the text node */
        pointer-events     : none;

        font-size          : var(--b-resource-utilization-bar-legend-font-size);
        fill               : var(--b-resource-utilization-bar-legend-text-color);
    }

    rect,
    rect.b-series-index-0 {
        fill : var(--b-resource-utilization-bar-color);

        &:hover {
            fill : var(--b-resource-utilization-bar-hover-color);
        }

        &.b-underallocated {
            --b-resource-utilization-bar-color       : var(--b-resource-utilization-underallocated-bar-color);
            --b-resource-utilization-bar-hover-color : var(--b-resource-utilization-underallocated-bar-hover-color);
        }
        &.b-series-index-0.b-overallocated,
        &.b-overallocated {
            --b-resource-utilization-bar-color       : var(--b-resource-utilization-overallocated-bar-color);
            --b-resource-utilization-bar-hover-color : var(--b-resource-utilization-overallocated-bar-hover-color);
        }
    }
}

.b-resource-row {
    .b-resource-utilization-histogram {

        svg g text.b-bar-legend {
            font-size : var(--b-resource-utilization-resource-bar-legend-font-size);
            fill      : var(--b-resource-utilization-resource-bar-legend-text-color);
        }

        --b-resource-utilization-bar-color       : var(--b-resource-utilization-resource-bar-color);
        --b-resource-utilization-bar-hover-color : var(--b-resource-utilization-resource-bar-hover-color);

        .b-overallocated {
            --b-resource-utilization-resource-bar-legend-text-color : var(--b-resource-utilization-resource-overallocated-text-color);
            --b-resource-utilization-bar-color                      : var(--b-resource-utilization-resource-overallocated-bar-color);
            --b-resource-utilization-bar-hover-color                : var(--b-resource-utilization-resource-overallocated-bar-hover-color);
        }

        .b-underallocated {
            --b-resource-utilization-resource-bar-legend-text-color : var(--b-resource-utilization-resource-underallocated-text-color);
            --b-resource-utilization-bar-color                      : var(--b-resource-utilization-resource-underallocated-bar-color);
            --b-resource-utilization-bar-hover-color                : var(--b-resource-utilization-resource-underallocated-bar-hover-color);
        }
    }
}

.b-assignment-row,
.b-composite-assignment-row {
    .b-resource-utilization-histogram {

        svg g text.b-bar-legend {
            font-size : var(--b-resource-utilization-assignment-bar-legend-font-size);
            fill      : var(--b-resource-utilization-assignment-bar-legend-text-color);
        }

        rect,
        rect.b-series-index-0 {
            --b-resource-utilization-bar-color       : var(--b-resource-utilization-assignment-bar-color);
            --b-resource-utilization-bar-hover-color : var(--b-resource-utilization-assignment-bar-hover-color);

            &.b-underallocated {
                --b-resource-utilization-assignment-bar-color       : var(--b-resource-utilization-assignment-underallocated-bar-color);
                --b-resource-utilization-assignment-bar-hover-color : var(--b-resource-utilization-assignment-underallocated-bar-hover-color);
            }

            &.b-overallocated {
                --b-resource-utilization-assignment-bar-color       : var(--b-resource-utilization-assignment-overallocated-bar-color);
                --b-resource-utilization-assignment-bar-hover-color : var(--b-resource-utilization-assignment-overallocated-bar-hover-color);
            }
        }
    }
}

.b-histogram-bar-tooltip {
    .b-on-date {
        font-weight : 500;
    }

    .b-summary-data {
        margin-block : .5em;

        .b-effort-data {
            display : flex;

            .b-effort-label {
                flex : 1;
            }
        }
    }

    .b-effort-range-data,
    .b-cost-data,
    .b-quantity-data {
        display : flex;

        .b-effort-label {
            flex : 1;
        }
    }

    .b-effort-value {
        &.b-underallocated {
            color : var(--b-resource-utilization-underallocated-text-color);
        }
        &.b-overallocated {
            color : var(--b-resource-utilization-overallocated-text-color);
        }
    }

    .b-assignments {
        display    : grid;
        column-gap : .5em;
    }

    .b-assignment {
        display : contents;

        .b-assignment-name {
            grid-column : 1;
        }

        .b-effort-data {
            display: flex;
            grid-column : 2;
            text-align  : end;
        }

        .b-cost-data {
            grid-column : 3;
            text-align  : end;
        }

        .b-quantity-data {
            grid-column : 4;
            text-align  : end;
        }

        .b-effort-label {
            display : none;
        }

        .b-effort-value {
            width : 100%;
        }
    }
}
