:root, :host {
    /**
     * Task editor padding
     */
    --b-task-editor-padding                 : var(--b-panel-with-header-padding);

    /**
     * Task editor occurrence info primary color
     */
    --b-task-editor-occurrence-info-primary : var(--b-color-red);

    /**
     * Task editor bottom button width, looks neater if all buttons have the same width
     */
    --b-task-editor-bottom-button-width     : 6.5em;
}

/* .b-bryntum { */
    /**
     * Task editor occurrence info background color, a shade of its primary color
     */
    /*--b-task-editor-occurrence-info-background : var(--b-primary-97);*/
/* } */

.b-task-editor-base {
    --b-tab-padding           : 0 .75em;
    --b-tab-icon-only-padding : 0 .75em;

    > .b-panel-body-wrap > .b-popup-content {
        padding        : var(--b-task-editor-padding);
        flex-direction : column;
    }

    &:not(.b-read-only) .b-popup-content {
        padding-bottom : 0;
    }

    > .b-panel-body-wrap > .b-bottom-toolbar .b-button {
        min-width : var(--b-task-editor-bottom-button-width);
    }
}

.b-occurrence-info-toolbar {
    --b-primary            : var(--b-task-editor-occurrence-info-primary);
    --b-toolbar-background : var(--b-task-editor-occurrence-info-background, var(--b-primary-97));
    --b-toolbar-padding    : .5em var(--b-panel-padding);
}
