.b-calendar-editor-week-panel {
    flex : 1;
    align-content : flex-start;

    .b-availability-day {
        gap : 0;

        .b-slide-toggle {
            margin-inline : auto 1em;
        }
    }

    .b-day-paste {
        display : none;
    }

    .b-divider {
        margin : 0;
    }

    [data-ref="container"] {
        align-items : flex-start;
        width       : 11em;
        padding-top : 1em;
    }

    [data-ref="weekContainer"] {
        &.b-copying {
            .b-availability-day.b-copied {
                .b-day-copy {
                    display : inherit;
                }

                .b-day-paste {
                    display : none;
                }
            }

            .b-availability-day:not(.b-copied) {
                .b-day-copy {
                    display : none;
                }

                .b-day-paste {
                    display : inherit;
                }
            }
        }
    }
}

