:root, :host {
    /**
     * Highlight border style
     */
    --b-calendar-highlight-border-style       : dashed;

    /**
     * Highlight border width
     */
    --b-calendar-highlight-border-width       : 2px;

    /**
     * Highlight background transparency (primary is color mixed with transparent xx)
     */
    --b-calendar-highlight-background-transparency : 50%;
}

