.b-sch-highlighted-calendar-range {
    position           : absolute;
    display            : grid;
    place-items        : center;
    animation-name     : b-anim-fade-in;
    animation-duration : 0.2s;
    overflow           : hidden;

    color              : var(--b-calendar-highlight-color, var(--b-primary-60));
    background-color   : var(--b-calendar-highlight-background, color-mix(in srgb, var(--b-primary-95), transparent var(--b-calendar-highlight-background-transparency)));
    border             : var(--b-calendar-highlight-border-width) var(--b-calendar-highlight-border-style) var(--b-calendar-highlight-border-color, color-mix(in srgb, var(--b-calendar-highlight-color, var(--b-primary-60)), transparent var(--b-calendar-highlight-background-transparency)));

    .b-sch-event-content {
        padding    : .5em;
        text-align : center;

        font-size  : var(--b-sch-event-font-size);
    }
}
