/* @after SchedulerPro/widget/taskeditor/FormTab.css */

.b-advanced-tab.b-form-tab.b-label-align-before.b-container {
    grid-template-columns : max-content 1fr 8em max-content;

    .b-label {
        white-space: normal;
    }

    /* Text fields on the left */
    > .b-text-field.b-has-label {
        > .b-label {
            grid-column : 1;
        }

        > .b-field-inner {
            grid-column : 2;
        }
    }

    /* Slide toggles on the right */
    > .b-slide-toggle.b-has-label {
        > .b-label {
            grid-column : 3;
        }

        > .b-field-inner {
            grid-column : 4;
        }
    }
}
