/* @after SchedulerPro/widget/taskeditor/EditorTab.css */

:root, :host {
    /**
     *  Padding for task editor form tabs
     */
    --b-form-tab-padding : var(--b-container-gap) 0 2px 0; /* Bottom to make sure outlines are visible */
}

.b-form-tab,
.b-recurrence-tab:not(.b-tab),
.b-overlay-scrollbar .b-form-tab.b-container.b-vertical-overflow {
    padding : var(--b-form-tab-padding);
}

.b-form-tab.b-container.b-widget {
    &.b-label-align-before {
        > .b-widget.b-has-label > .b-field-inner {
            grid-column : span 3;
        }

        > .b-half-width.b-widget.b-has-label > .b-field-inner {
            grid-column : span 1;
        }
    }

    .b-divider {
        grid-column : 1 / -1;
    }
}

.b-visible-scrollbar .b-form-tab.b-container.b-widget.b-vertical-overflow {
    padding-inline-end : var(--b-container-gap);
}
