:root, :host {
    /**
     * Percent bar z-index
     */
    --b-percent-bar-z-index           : 1;

    /**
     * Percent bar blend mode
     */
    --b-percent-bar-blend-mode        : multiple;

    /**
     * Percent bar handle text color
     */
    --b-percent-bar-handle-text-color : var(--b-grid-cell-color);
}

.b-percent-bar {
    .b-sch-event-wrap,
    .b-sch-event {
        overflow : visible;
    }
}

.b-task-percent-bar-outer {
    position      : absolute;
    inset         : 0;
    overflow      : clip;
    transition    : background-color 0.2s;

    border-radius : calc(var(--b-sch-event-border-radius) - var(--b-sch-event-border-width, 0px));
}

.b-task-percent-bar {
    /**
     * Percent bar background. Note that for color-mix to work, this var is defined on .b-task-percent-bar
     */
    --b-percent-bar-background : color-mix(in srgb, var(--b-primary), transparent 70%);

    border-start-start-radius  : inherit;
    border-end-start-radius    : inherit;
    box-sizing                 : content-box;
    display                    : flex;
    justify-content            : flex-end;
    align-items                : flex-end;
    /* If outside page uses line-height on BODY tag it will displace the % text */
    line-height                : initial;
    /* Always transition color, for hover etc. */
    transition                 : color 0.2s, background-color 0.2s, opacity 0.2s;
    mix-blend-mode             : var(--b-percent-bar-blend-mode);

    background                 : var(--b-percent-bar-background);
    z-index                    : var(--b-percent-bar-z-index);

    .b-sch-horizontal &,
    .b-gantt & {
        height     : 100%;
        max-width  : 100%;
        text-align : right;
    }

    .b-sch-vertical & {
        width      : 100%;
        max-height : 100%;
    }

    /* Only transition width while animating changes */
    .b-animating & {
        transition : color 0.2s, width 0.2s, height 0.2s;
    }

    /* No transition on width when resizing */
    .b-task-percent-bar-resizing-task & {
        transition : background-color 0.2s;
    }
}

.b-task-percent-bar-handle {
    position           : absolute;
    width              : 0;
    height             : 0;
    display            : flex;
    justify-content    : center;
    z-index            : 1000;
    border-color       : #fff;
    border-right-color : transparent;
    border-left-color  : transparent;
    border-width       : 0 5px 6px 5px;
    border-style       : solid;
    opacity            : 1;

    .b-sch-horizontal &,
    .b-gantt & {
        cursor              : ew-resize !important;
        bottom              : 0;
        margin-inline-start : -5px;
    }

    .b-gantt-task-parent & {
        bottom : 8px;
    }

    .b-sch-vertical & {
        cursor     : ns-resize !important;
        left       : 0;
        margin-top : -4px;
        transform  : rotate(90deg);
    }

    .b-sch-horizontal .b-sch-event & {
        margin-inline-start : -8px;
        border-width        : 0 6px 7px 7px;
    }

    .b-sch-vertical .b-sch-event & {
        margin-inline-start : -3px;
        border-width        : 0 6px 7px 7px;
    }

    .b-sch-event.b-sch-event-selected & {
        z-index : 1000;
    }
}

/* Display xx% */
.b-task-percent-bar-handle::after {
    position   : absolute;
    margin-top : 10px;
    font-size  : .8em;

    color      : var(--b-percent-bar-handle-text-color);
}

.b-task-percent-bar-show-percentage:where(:not(.b-resizing-event, .b-dragging-event)) {
    &.b-task-percent-bar-resizing-task,
    .b-sch-event-hover {
        .b-task-percent-bar-handle::after {
            content : attr(data-percent) "%";
        }
    }
}

.b-sch-vertical .b-task-percent-bar-handle::after {
    margin-top : -2em;
    rotate     : -90deg;
}

.b-task-percent-bar-resizing-task,
.b-task-percent-bar-resizing-event {
    .b-sch-event,
    .b-gantt-task {
        user-select         : none;
        -webkit-user-select : none;
        cursor              : ew-resize !important;
        overflow            : visible;
    }
}

.b-task-percent-bar-drag-disabled .b-task-percent-bar-handle,
.b-read-only .b-task-percent-bar-handle {
    display : none !important;
}
