/* Shared between the Week tab and the Exception tab */
.b-calendar-editor-base-tab {
    flex-flow : nowrap;

    .b-grid[data-ref="grid"] {
        flex                             : 0 0 15em;
        margin-top                       : 1.5em;

        border                           : var(--b-text-field-outlined-border-width) solid var(--b-text-field-outlined-border-color);
        border-radius                    : var(--b-widget-border-radius);

        --b-panel-bottom-toolbar-padding : 0.5em;
        --b-toolbar-gap                  : 0;
        --b-grid-cell-padding-inline     : 0;

        .b-grid-row.b-invalid .b-grid-cell {
            color : var(--b-text-field-invalid-border-color);
        }

        .b-grid-cell.b-color-cell .b-color-cell-swatch {
            border-radius : var(--b-widget-border-radius);
            margin-inline : 0;
        }
    }

    .b-calendar-editor-form {
        grid-template-columns : max-content 1fr 4em max-content 1fr 4em;
        padding-top           : 1.5em;

        [data-ref="nameField"],
        [data-ref="exceptionStartDateField"],
        [data-ref="isWorkingField"] {
            > .b-label {
                width : 3.5em;

            }
        }

        [data-ref="nameField"] {
            grid-column : span 5;
        }

        [data-ref="startDateField"],
        [data-ref="endDateField"],
        [data-ref="exceptionStartDateField"],
        [data-ref="exceptionEndDateField"] {
            grid-column : span 3;
        }

        [data-ref="isWorkingField"] {
            grid-column : span 5;
            gap         : var(--bi-field-gap);
        }

        .b-span-row {
            grid-column : 1 / -1;
        }

        .b-color-field {
            input {
                max-width      : 0;
                padding-inline : 0;
            }

            .b-color-box {
                margin-inline-start : 1.25em;
            }
        }
    }

    .b-widget[data-ref="panel"] {
        overflow-y : auto;
        height     : 100%;
    }

    .b-availability-range {
        align-items : center;
    }
}
