:root, :host {
    /**
     * Parent event header height, to display the name at top
     */
    --b-nested-events-header-height : 20px;

    /**
    * Parent event color
    */
    --b-nested-events-parent-color                  : var(--b-neutral-40);

    /**
     * Parent event background
     */
    --b-nested-events-parent-background             : transparent;

    /**
     * Parent event hover background
     * @state Hovered
     */
    --b-nested-events-parent-hover-background       : transparent;

    /**
     * Parent event selected background
     * @state Selected
     */
    --b-nested-events-parent-selected-background    : transparent;

    /**
     * Nested event container background (below the header in a parent event)
     */
    --b-nested-events-container-background          : var(--b-neutral-97);

    /**
     * Nested event container hover background
     * @state Hovered
     */
    --b-nested-events-container-hover-background    : var(--b-neutral-93);

    /**
     * Nested event container selected background
     * @state Selected
     */
    --b-nested-events-container-selected-background : var(--b-neutral-91);
}

.b-sch-event-wrap.b-nested-events-parent {
    > .b-sch-event:where(:not(.b-milestone)) {
        --b-sch-event-color               : var(--b-nested-events-parent-color);
        --b-sch-event-background          : var(--b-nested-events-parent-background);
        --b-sch-event-hover-background    : var(--b-nested-events-parent-hover-background);
        --b-sch-event-selected-background : var(--b-nested-events-parent-selected-background);

        border-inline-start               : none;
        flex-direction                    : column;
        width                             : 100%; /* To not cut 1px away */

        /* Hovering empty space */
        &:hover:not(:has(.b-sch-event:hover)) {
            --b-nested-events-container-background : var(--b-nested-events-container-hover-background);
        }

        /* Turn event content into a thin header at top */
        > .b-sch-event-content {
            padding-block : 0.1em;
            flex          : 0 0 var(--b-nested-events-header-height);
            min-height    : 0; /* Needed for FF to respect flex basis correctly */
        }

        /* Hide resize handles */
        &::before,
        &::after {
            display : none;
        }
    }

    &.b-sch-style-traced,
    &.b-sch-style-bordered,
    &.b-sch-style-outlined {
        --b-nested-events-container-background          : transparent;
        --b-nested-events-container-hover-background    : transparent;
        --b-nested-events-container-selected-background : transparent;
    }

    &.b-sch-style-traced,
    &.b-sch-style-bordered {
        --b-nested-events-parent-background          : var(--b-neutral-95);
        --b-nested-events-parent-hover-background    : var(--b-neutral-93);
        --b-nested-events-parent-selected-background : var(--b-neutral-91);
    }

    &.b-sch-style-rounded > .b-sch-event:where(:not(.b-milestone)) {
        border-radius : 0;

        .b-nested-events-container {
            border-radius : var(--b-sch-event-border-radius);
        }
    }
}

.b-nested-events-container {
    position      : relative;
    align-self    : flex-start;
    flex          : 1;
    width         : 100%;
    overflow      : visible;
    transition    : background .2s;
    border-radius : inherit;

    background    : var(--b-nested-events-container-background);

    .b-sch-event-wrap.b-selected & {
        --b-nested-events-container-background : var(--b-nested-events-container-selected-background);
    }

    .b-sch-event:where(:not(.b-milestone)) {
        width : 100%; /* To not cut 1px away */
    }

    .b-sch-event {
        /* Compensate for border on the left side */
        translate : calc(var(--b-sch-event-border-width) * -1);
    }

    &.b-nested-events-layout-stack {
        overflow-y : auto;
        overflow-x : hidden;
    }
}

.b-dependencies:not(.b-dependency-only-parent) {
    /* No scrolling of containers when using dependencies, want overflow visible to not clip terminals */
    .b-nested-events-parent > .b-sch-event,
    .b-nested-events-container.b-nested-events-layout-stack {
        overflow : visible;
    }

    /* Also hoist hovered nested event to make sure others do not clip terminals */
    .b-nested-event.b-sch-event-hover {
        z-index : 9;
    }
}

/* Turn things around for vertical mode */
.b-sch-event-wrap.b-nested-events-parent.b-sch-vertical {
    > .b-sch-event:where(:not(.b-milestone)) {
        flex-direction : row;
        padding        : 0;

        > .b-sch-event-content {
            writing-mode : vertical-rl;
            transform    : rotate(180deg);
        }
    }

    .b-nested-events-container {
        height : 100%;
    }
}

/* Move deps in front of events when using nested events */
.b-nested-events .b-sch-dependencies-canvas {
    z-index : 8;
}
