.b-sch-highlight-container {
    position           : absolute;
    top                : 0;
    inset-inline-start : 0;
    pointer-events     : none;
}

.b-sch-highlighted-range {
    position            : absolute;
    display             : flex;
    align-items         : center;
    justify-content     : center;
    transition-property : opacity;
    transition-duration : 0.3s;

    color               : var(--b-time-span-highlight-range-color, var(--b-primary-40));
    font-size           : var(--b-time-span-highlight-range-font-size);
    opacity             : var(--b-time-span-highlight-range-opacity);

    &.b-unavailable {
        color      : var(--b-time-span-highlight-unavailable-color);
        background : var(--b-time-span-highlight-unavailable-background);
    }
}

.b-sch-highlighted-range-name {
    text-overflow : ellipsis;
    overflow      : hidden;
}

.b-sch-highlighted-range-default {
    background : var(--b-time-span-highlight-range-background, var(--b-primary-95));
    border     : 2px dashed var(--b-time-span-highlight-range-border-color, var(--b-primary-70));
}

.b-timeline-base.b-transition-highlight {
    .b-sch-highlighted-range[data-sync-id] {
        transition-property : width, height, inset-inline-start, top, opacity;
    }
}

@starting-style {
    .b-sch-highlighted-range {
        opacity : 0;
    }
}
