.b-calendar-editor-legend {
    display : block;
    padding : 0;

    & > ul {
        padding    : 0;
        margin-top : 0;

        li {
            display       : flex;
            align-items   : center;
            margin-bottom : 0.5em;

            &.b-interval-legend {
                gap : 0; /* Override gap from b-icon */

                &:hover {
                    cursor          : pointer;
                    text-decoration : underline;
                }
            }

            &:before {
                content           : '';
                display           : inline-block;
                text-align        : center;
                margin-inline-end : 1em;
                width             : 1.5em;
                height            : 1.5em;
                line-height       : 1.5em;
                background        : currentColor;
                vertical-align    : middle;

                border-radius     : var(--b-widget-border-radius);
            }

            &.b-working-day,
            &.b-non-working-day {
                line-height : 2em;
            }

            &.b-working-day {
                margin-bottom : 0.8em;
            }

            &.b-non-working-day {
                margin-bottom : 1.5em;
            }

            &.b-non-working-day:before,
            &.b-working-day:before {
                border        : solid 1px #bbb;
                border-radius : 50%;
            }

            &.b-non-working-day:before {
                background : var(--b-calendar-editor-non-working-day-background);
            }

            &.b-working-day:before {
                background : var(--b-calendar-editor-working-day-background);
            }

            &.b-interval-legend:before {
                color   : var(--b-primary);
                display : inline-block;
            }

            &.b-interval-legend:hover:before {
                text-decoration : none;
            }
        }
    }
}
