.b-calendar-editor-availability-range-container {
    flex : 1;

    .b-flex-row {
        align-items : center;
    }

    .b-availability-range {
        .b-time-field {
            width : 6.6em;
        }

        [data-ref="addRangeButton"] {
            margin-inline-start : auto;
        }

        &:only-child {
            [data-ref="removeRangeButton"] {
                display : none;
            }
        }

        &:not(:first-child) {
            [data-ref="addRangeButton"] {
                display : none;
            }
        }
    }
}
