:root, :host {
    /**
     * Event buffer line color
     */
    --b-event-buffer-color       : var(--b-border-6);

    /**
     * Event buffer label color
     */
    --b-event-buffer-label-color : var(--b-text-4);
}

.b-event-buffer {
    .b-sch-event:where(:not(.b-milestone)) {
        width : 100%;
    }

    .b-sch-event-buffer-before {
        grid-area : start;
    }

    .b-sch-event-buffer-after {
        grid-area : end;
    }

    &.b-sch-horizontal .b-sch-event-wrap {
        .b-sch-event-buffer {
            align-items : center;
            height      : 5px;

            background  : repeating-linear-gradient(180deg, transparent, transparent 30%, var(--b-event-buffer-color) 30%, var(--b-event-buffer-color) 70%, transparent 70%, transparent 100%);
        }

        .b-sch-event-buffer-before {
            flex-direction  : row;
            justify-content : flex-end;

            .b-buffer-label {
                margin-inline-end  : auto;
                padding-inline-end : 0.5em;
            }

            .b-buffer-label,
            i {
                translate : .1em -1em;
            }

            i {
                margin-inline-end : .5em;
            }
        }

        .b-sch-event-buffer-after {
            flex-direction  : row-reverse;
            justify-content : flex-end;

            .b-buffer-label,
            i {
                translate : -.1em -1em;
            }

            .b-buffer-label {
                text-align          : right;
                margin-inline-start : .5em;
            }

            i {
                margin-inline-start  : auto;
                padding-inline-start : 0.5em;
            }
        }
    }

    &.b-sch-vertical .b-sch-event-wrap {
        contain : size layout;

        .b-sch-event-buffer {
            width        : 5px;
            justify-self : center;

            background   : repeating-linear-gradient(90deg, transparent, transparent 30%, var(--b-event-buffer-color) 30%, var(--b-event-buffer-color) 70%, transparent 70%, transparent 100%);
        }

        .b-sch-event-buffer-before {
            grid-area       : above;
            flex-direction  : column;
            justify-content : flex-end;

            .b-buffer-label {
                margin-inline-end : .5em;
            }
        }

        .b-buffer-label,
        i {
            margin-right : -1.3em;
            writing-mode : vertical-rl;
        }

        .b-sch-event-buffer-after {
            grid-area       : below;
            flex-direction  : column-reverse;
            justify-content : flex-end;

            .b-buffer-label {
                margin-inline-start : .5em;
            }
        }
    }

    .b-sch-event-buffer {
        display     : flex;
        white-space : nowrap;
        font-size   : 10px;

        color       : var(--b-event-buffer-label-color);
    }
}

.b-animating {
    .b-sch-event-buffer {
        transition : width .2s;
    }
}
