:root, :host {
    /**
     * Event segment link width
     */
    --b-segment-link-width   : 1px;

    /**
     * Event segment link style
     */
    --b-segment-link-style   : dashed;

    /**
     * Event segment link color
     */
    --b-segment-link-color   : var(--b-border-4);

    /**
     * Event segment background
     */
    --b-segmented-background : transparent;
}

/* Remove border & background from the segment container */
.b-gantt-task-wrap,
.b-sch-event-wrap {
    .b-segmented {
        border-width  : 0;
        border-radius : 0;
        width         : 100%;
        padding       : 0;

        background    : var(--b-segmented-background);
    }
}

.b-sch-event-segments {
    position       : absolute;
    top            : 0;
    width          : 100%;
    height         : calc(50% + 0.5px);
    pointer-events : none;

    border-bottom  : var(--b-segment-link-width) var(--b-segment-link-style) var(--b-segment-link-color);

    .b-sch-event-segment {
        pointer-events : all;
        position       : absolute;
        display        : flex;
        align-items    : center;
    }
}

