.b-calendar-editor-date-picker {
    --b-toolbar-padding                          : 0;
    --b-calendar-panel-gap                       : .57em;
    --b-date-picker-min-width                    : 0;
    --b-panel-with-header-top-toolbar-background : transparent;

    .b-calendar-panel-cell {
        &:not(.b-selected-date) .b-date-picker-cell-inner {

            &.b-non-working-day {
                background : var(--b-calendar-editor-non-working-day-background);
            }

            &.b-working-day {
                background : var(--b-calendar-editor-working-day-background);
            }

        }

        .b-date-picker-cell-inner {
            .b-interval-badges {
                position           : absolute;
                bottom             : 0;
                inset-inline-start : 0.4em;
                display            : flex;

                .b-interval-badge {
                    &:before {
                        content          : " ";
                        min-width        : 0.5em;
                        min-height       : 0.5em;
                        z-index          : 5;
                        display          : flex;
                        justify-content  : center;
                        align-items      : center;
                        border-radius    : 2px;
                        background-color : var(--b-primary);
                    }
                }
            }
        }
    }

}
