/* @after SchedulerPro/widget/taskeditor/FormTab.css */

.b-scheduler-advanced-tab {
    &.b-container.b-widget.b-label-align-before {
        /* Text fields on the left */
        > .b-text-field.b-has-label {
            > .b-label {
                grid-column : 1;
            }

            > .b-field-inner {
                grid-column : 2;
            }
        }

        /* Slide toggles on the right */
        > .b-slide-toggle.b-has-label {
            > .b-label {
                grid-column : 3;
            }

            > .b-field-inner {
                grid-column : 4;
            }
        }
    }
}
