:root, :host {
    /**
     * Calendar editor non-working day background color
     */
    --b-calendar-editor-non-working-day-background : color-mix(in srgb, var(--b-color-red), transparent 90%);

    /**
     * Calendar editor working day background color
     */
    --b-calendar-editor-working-day-background     : transparent;
}

.b-calendar-editor {
    .b-tab-panel {
        height : 100%;
    }

    .b-tab-panel-item {
        height : 100%;
    }

    .b-widget.b-html {
        user-select         : none;
        -webkit-user-select : none;
    }

    .b-calendar-editor-content {
        padding-top : 0;
    }

    [data-ref="generalTab"] {
        padding-top   : 1.5em;
        flex-wrap     : wrap;
        align-content : flex-start;
        overflow-y    : auto;

        [data-ref="divider"] {
            height : 1px;
            margin : 0;
        }
    }
}
